.about-header {
    margin-left: 10vw;
    margin-right: 10vw;
    font-family: 'Ubuntu';
    font-size: 2vh;
}
.about-container{
    display: flex;
    flex-direction: row;
    /* flex-wrap: wrap; */

}

.about-container p {
    border-bottom: 0.27vh solid #111;
}

.about-container h1{
    background-image: url("./assets/dust-and-scratches.jpg");
    display: inline-block;
    color: white;
    padding-left: 1vw;
    padding-right: 1vw;
    margin-bottom: -5vh;
    font-size: 3.5vh;
}

.about-container h3 {
    font-size: 2vh;
    padding-left: 1.5vw;
    margin: 0;
}
.about-container i {
    padding: 0;
    transition: 0.2s ease-out;
}

.about-container i:hover {
    filter: invert(1);
}

.about-contact{
    width: 20vw;
}

.about-exp{
    width: 60vw;
    margin-left: 3vw;
    
}

.about-exp h4  {
    font-size: 3vh;
}
.about-exp h6  {
    font-size: 2.5vh;
}

.about-contact-items {
    display: flex;
    flex-direction: row;
    margin-bottom: 2vh;
}

.about-skills-icons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 77.7vw;
}


