@media screen
and (min-width: 240px)
and (max-width: 720px){
    .about-container{
        flex-direction: column;
        text-align: center;
        /* flex-wrap: wrap; */
    
    }
    .about-contact{
        font-size: 3vh;
        width: 95vw;
        padding-left: 1vw;
    }
    .about-exp{
        margin: 0;
        font-size: 3vh;
        width: 95vw;
        padding-left: 1vw;
    }
    .about-exp span {
        display: none;
    }
    .about-exp h4 {
        float: left;
    }

    .about-skills {
        font-size: 3vh;
        padding-left: 1vw;
    }
    .about-contact-items {
        display: flex;
        flex-direction: column;
        margin-bottom: 2vh;
    }
    .about-container i {
        padding: 0;
        text-align: center;
        align-items: center;
        /* justify-content: center; */
    }
    

}