/*For Toggle Button -- Dark Mode*/
.switch {
    position: relative;
    /* display: inline-block; */
    display: none;
    width: 5%;
    width: 60px;
    height: 34px;
    margin-left: 38vh;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #333;
    transition: .4s;
    
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background: white;
    border: 1px solid #333;
    transition: .6s;
  }

  input:checked + .slider {
    background-color: white;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 2px #333;
  }
  
  input:checked + .slider:before {
    transform: translateX(26px);
    background-color: #333;

  }
      
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

  
