.featured-box-one {
    background-image: url("https://colah.github.io/posts/2014-03-NN-Manifolds-Topology/img/simple2_1.png");
    background-position: center;
    background-size: cover;
    height: 50vh;
    border-radius: 10px 10px 0 0;
    position: relative;
}

.featured-box-one h5 {
    position: absolute;
    background-image: url("../assets/dust-and-scratches.jpg");
    color: #ffffff;
    bottom: 10px;
    left: 20px;
    padding: 3px 8px;
    border-radius: 30px;
    display: inline-block;
    text-transform: uppercase;
    text-align: center;
    font-size: 2vh;
}


