.card-blog {
    
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin: 0 1vw 0 1vw;
    
    transition: all 300ms ease;
    
}
.card-blog h3 {
    width: 22vw;
    text-align: center;
    font-weight: bolder;
    font-size: 2vh;
    padding: 1vh 2vh 1vh 2vh;
    overflow: hidden;
}

.card-blog:hover {
    transform: translateY(-5px);
}